
#header {
  position: relative;
}
#header .h-title {
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  font-size: 0.35rem;
  font-weight: bold;
  background-color: rgba(78, 27, 189, 0.43);
}
#header .h-bar,
#header .h-search {
  position: absolute;
  top: 0;
  height: 1rem;
  line-height: 1rem;
  font-size: 0.5rem;
  z-index: 2;
  color: #333;
}
#header .h-bar {
  left: 0.2rem;
}
#header .h-search {
  right: 0.3rem;
}



